@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Text&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont,'DM Serif Text', serif, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


.header {
    height: 60px;
    width: 100%;
    background-color: #111d5e;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    margin: 0;
    position: fixed;
    z-index: 200;
}

.logoName {
    color: #fff;
    padding-left: 5px;
}
.hamburgerBox {
    margin-right: 5px;
    width: 30px;
    height: 20px;
    display: grid;
    grid-row: auto;
    justify-content: center;
}
.hamburgerLine {
    height: 3px;
    width: 20px;
    background-color: #fff;
}
/***  Toolbar Style  ***/
.toolbar-nav {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar-nav-items a {
    color: #fff;
    text-decoration: none;
}

.toolbar-nav-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar-nav-items li {
    padding: 0 0.5rem;
}

.toolbar-nav-items a:hover,
.toolbar-nav-items a:active,
.toolbar-nav-items a.active {
    color: #8898ec;
}

@media (max-width: 767px) {
    .toolbar-nav-items {
        display: none;
    }

}

.toolbar-nav-items a.active::after,
.toolbar-nav-items a:hover::after {
    display: block;
    height: 1px;
    background-color: #8898ec;
    content: ' ';
    width: 50px;
    margin: 0 auto;
}

/***  Navbar Style  ***/
.back-drop {
    height: 100vh;
    width: 100%;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.3);
    position: fixed;
    top: 0;
    left: 0;
}
.side-nav {
    height: 100%;
    width: 60%;
    max-width: 350px;
    box-shadow: 1px 0px 7px rgba(0,0,0,0.5);
    position: fixed;
    top: 50px;
    right: 0;
    z-index: 300;
    background-color: #eee;
    text-align: center;
    justify-content: center;
}
.side-nav a {
    text-decoration: none;
    font-size: 120%;
    color: rgb(13,23,48);
}

.side-nav a:hover,
.side-nav a:active,
.side-nav a.active {
    color: tomato;
}
.side-nav ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    padding: 0;
}
.side-nav li {
    padding: 15px 0;
}

/***  Hamburger Button  ***/
.toggle-btn {
	width: 30px;
	height: 24px;
	background: rgb(16, 16, 66);
	display: flex;
	cursor: pointer;
	padding: 0;
	box-sizing: border-box;
	border-style: none;
}

.hamburger {
    display: block;
	position: absolute;
	top: 0;
	right: 0;
	width: 30px;
	padding: 1rem;
	margin-top: 7px;
}
/* Hamburger line */
.hamburger div {
	position: relative;
	width: 100%;
	height: 2px;
    background-color: white;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.4s ease;
}
.hamburger div:before,
.hamburger div:after {
	content: '';
	position: absolute;
	z-index: 1;
	top: -10px;
	width: 100%;
	height: 2px;
	background: inherit;
}
.hamburger div:after {
	top: 10px;
}
/* Toggle animation */
.open > div {
    transform: rotate(135deg);
	background-color: #fff;
}
/* Turn lines into X */
.open > div:before ,
.open > div:after {
	top: 0;
    transform: rotate(90deg);
	background-color: #fff;
}

@media (min-width: 767px) {
    .hamburger {
        display: none;
    }
}


.container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    text-align: center;
    background-color: #eee;
}
.homePageContainer {
    width: 100%;
}

.mainSection {
    color: #fff;
}

.headerText {
    width: 70%;
    height: 415px;
    margin: 75px auto;
    padding-left: 15px;
    text-align: center;
    font-size: 1.5rem;    
}

.headerText h1 {
    margin-bottom: 4px;
}
.headerText h3 {
    margin-top: 4px;
}
.mainImg {
    height: 89vh;
    width: 100%;
    background-image: url(/eduCoach/static/media/pexels-andrea-piacquadio-3932232.efe1f7ec.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

.imgCover {
    background-color: rgba(0,0,0,0.5);
    height: 89vh;
    width: 100%;
    position: absolute;
    z-index: 100;
}

.bookNow {
    width: 10%;
    height: 50px;
    background-color: #c70039;
    position: fixed;
    border-bottom-left-radius: 20px;
    top: 60px;
    right: 0;
    z-index: 200;
}

img {
    width: 60px;
    height: 45px;
}

.chatIcon {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 200;
}

/***   Home Page Details  ***/
.detailsPart {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
}
.detailsContainer {
    width: 250px;
    height: 400px;
    background-color: #fff;
    border: 2px solid rgba(177, 177, 218, 0.842);
    margin: 8px auto;
}


/***  Responsive for small screen  ***/
@media only screen and (max-width: 767px) {
    .mainImg {
        background-size: contain;
        margin-top: 60px;
        height: 40vh;
    }
    .imgCover {
        height: 40vh;
    }
    .bookNow {
        width: 20%;
    }
    .headerText {
        width: 94%;
        height: 240px;
        margin: 15px auto;
        padding: 0;
        font-size: 100%;
        
    }
    .headerText p {
        margin: 5px;
    }
}

@media only screen and (min-width: 768px) {
    .homePageContainer {
        margin-top: 60px;
    }
}
.pageContainer {
    margin-top: 60px;
}
